/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #E45826;
  --red: #fa0303;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #1B1A17;
}

.App {
  /* background-image: url(data:image/png;base64,); */
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}

::-webkit-scrollbar{
  display: none;
}

.stroke-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #E6D5B8;


}

.btn{
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover{
  cursor: pointer;
}

.hero-buttons{
  display: flex;
  gap: 1rem;
  font-weight: normal;
}

.hero-buttons>:nth-child(1), .hero-buttons>:nth-child(2){
  color: #E6D5B8;
  background-color: rgb(2, 2, 2, 0.3);
  border: 2px solid var(--orange);
  width: 8rem;
}


.blur{
  background-color: #F0A500;
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}